import Home from './components/Menu/Home/Home.vue'
import Login from './components/Login/Login.vue'
import Register from './components/Register/Register.vue'
import CryptoCurrency from './components/Menu/CryptoCurrency/CryptoCurrency.vue'
import SiteSearch from './components/Menu/Home/SiteSearch.vue'
import Favorite from './components/Menu/Home/Favorite/Favorite.vue'


import MyArticle from './components/AccountMenu/MyArticle/MyArticle.vue'
import Setting from './components/AccountMenu/Setting.vue'
import Friend from './components/AccountMenu/Friend/Friend.vue'
import Message from './components/AccountMenu/Message/Message.vue'
import ReadMessage from './components/AccountMenu/Message/ReadMessage.vue'
import SendMessage from './components/AccountMenu/Message/SendMessage.vue'
import ArticleCollect from './components/AccountMenu/ArticleCollect/ArticleCollect.vue'
import PublishArticle from './components/Menu/CryptoCurrency/PublishArticle.vue'
import ModifySuggest from './components/Menu/CryptoCurrency/ModifySuggest.vue'
import ReadArticle from './components/Menu/CryptoCurrency/ReadArticle.vue'

const routes = [
	{ path: '/', component: Home },
    { path: '/cryptocurrency', component: CryptoCurrency },
    { path: '/blockchain', component: null },
    { path: '/login', component: Login },
    { path: '/register', component: Register },
    { path: '/site_search', component: SiteSearch},
    { path: '/favorite', component: Favorite},

    { path: '/setting', component: Setting},
    { path: '/myarticle', component: MyArticle},
    { path: '/friend', component: Friend},
    { path: '/message', component: Message},
    { path: '/read_message', component: ReadMessage},
    { path: '/send_message', component: SendMessage},
    {
        path: '/reply_message', component: SendMessage,
        props: {receiver: 'John Chen', replyTitle: 'Re: '}
    },
    { path: '/article_collect', component: ArticleCollect},
    { path: '/publish_article', component: PublishArticle, props: {board: '', discussTitle: 'Bitcoin'}},
    {
        path: '/reply_article',
        component: PublishArticle,
        props: {
            board: '',
            discussTitle: 'Bitcoin',
            replyTitle: ''
        }
    },

    {
        path: '/modify_suggest',
        component: ModifySuggest,
        props: {
            board: '',
            discussTitle: 'Bitcoin',
            topicTitle: '',
            originalContent: ''
        }
    },
    {
        path: '/read_article/:id',
        component: ReadArticle,
    },
    
];

export default routes;